<template>
  <div class="edit-product">
    <base-header type="gradient" class="pb-7 pt-7"> </base-header>
    <div class="container-fluid mt--7">
      <div class="row no-gutters justify-content-center">
         <div class="col-md-12 pl-3">
          <span>
            <i class="fas fa-chevron-left text-info" style="outline: 0;box-shadow: none!important;">
            </i>
            <router-link :to="{ name: 'Products'}" class="pl-2 text-info form-control-label" style="font-size: 16px;">Back</router-link>
          </span>
        </div>
      </div>

      <div class="row no-gutters justify-content-center mt-1 mb-6">
        <div class="col-12">


          <div class="card shadow bg-secondary mb-6">
            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row align-items-center">
                  <div class="col-12">
                    <h3 class="mb-0">Add Product</h3>
                  </div>
                </div>
              </div>
            </div>

            <div class="card-body">
              <form> 
                <h6 class="heading-small text-muted mb-4">Product information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                             Brand
                          </label>
                         <select class="form-control form-control-alternative" v-model="BrandID">
                            <option disabled value="">Please select one</option>
                            <option
                              v-for="item in brand_lists"
                              :key="item.id"
                              :value="item.id"
                            >
                              {{ item.BrandName }}
                            </option>
                          </select>
                        </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Category
                            </label>
                            <select class="form-control form-control-alternative" v-model="selectedCategory">
                               <option disabled value="">Please select one</option>
                              <option
                                v-for="item in tag_hirarchy"
                                :key="item.category.id"
                                :value="item.category.tagName"
                              >
                                {{ item.category.tagName }}
                              </option>
                            </select>
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Subcategory
                            </label>
                            <select class="form-control form-control-alternative" :disabled="selectedCategory === ''" v-model="selectedSubCategory">
                              <option disabled value="">Please select one</option>
                              <option
                                v-for="item in subCatList[selectedCategory]"
                                :key="item.id"
                                :value="item.tagName"
                              >
                                {{ item.tagName }}
                              </option>
                            </select>
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Code
                            </label>
                            <input
                              placeholder="Product Code"
                              id="productCode"
                              v-model="ProductCode"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-8">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Name
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="ProductName"
                              v-model="ProductName"
                              placeholder="Product Name"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>
                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Pricing Information</h6>
                <div class="">
                   <div class="row">
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Product Price
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="pricePerPiece"
                              v-model="ProductPrice"
                              placeholder="Price Per Piece"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               GST
                            </label>
                            <input
                              class="form-control form-control-alternative"
                              id="gstInput"
                              v-model="GST"
                              placeholder="GST"
                              type="text"
                            />
                         </div>
                      </div>
                      <div class="col-lg-4">
                         <div class="form-group has-label">
                            <label class="form-control-label">
                               Available Stocks
                            </label>
                            <input
                              placeholder="Quantity / Set"
                              id="qtyBundle"
                              v-model="Availability"
                              class="form-control form-control-alternative"
                              type="text"
                            />
                         </div>
                      </div>
                   </div>
                </div>


                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Product Description</h6>
                <div class="">
                  <div class="row">

                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label"  for="productDescription">
                              Product Description
                           </label>
                          <textarea rows="4" type="text" placeholder="ProductDescription" id="productDescription" v-model="ProductDesc" class="form-control form-control-alternative"></textarea>
                        </div>
                      </div>
                    </div>
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label"  for="colorDescription">
                              Color Description
                           </label>
                          <textarea type="text"  rows="4" placeholder="Color Description" id="colorDescription" v-model="ColorDesc" class="form-control form-control-alternative" ></textarea>
                        </div>
                      </div>
                    </div>

                    <div class="col-lg-12">
                      <div class="form-group">
                        <div class="form-group has-label">
                          <label class="form-control-label">
                            Upload Images (upto 5)
                          </label>
                          <div class="image-box image-dropzone">
                            <div id="preview" class="row" style="">
                              
                            </div>
                            <input 
                              type="file"
                              name="picture"
                              id="new-file"
                              accept="image/*"
                              ref="file"
                              v-on:change="handleImageUpload()"
                              class="custom-file-input"
                              aria-label="picture"
                              style="background-color: pink"
                              multiple
                            />     
                            <div class="dropzone-text">Drop Files in this area or Click to upload</div>                        
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <hr class="my-4">
                <h6 class="heading-small text-muted mb-4">Product Status</h6>
                <div class="">
                  <div class="row">
                    <div class="col-lg-6">
                      <div class="form-group">
                        <div class="form-group has-label">
                           <label class="form-control-label">
                              Status
                           </label>
                            <select class="form-control form-control-alternative" v-model="Status">
                              <option value="true">Active</option>
                              <option value="false">InActive</option>
                            </select>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

              </form>
            </div>

            <div class="card-header">
              <div class="bg-white border-0">
                <div class="row">
                  <div class="col-12 text-right">
                    <router-link type="primary" :to="{name:'Products'}" class="btn btn-secondary mr-2"
                      >Cancel</router-link>
                    <button
                      type="primary"
                      class="btn btn-info"
                      @click="addProduct()"
                      >Add Product</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default({
    name: "Add Product",
    data(){
        return {
            brand_lists:[],
            BrandID:"",
            ProductCode:"",
            ProductName:"",
            ProductPrice:"",
            GST:"",
            Discount:"",
            Availability:"",
            ProductDesc:"",
            ColorDesc:"",
            tag_hirarchy:[],
            subCatList:{},
            selectedCategory:'',
            selectedSubCategory:'',
            Status:true,
        }
    },
    async mounted() {
        let brands = await axios.get(`http://localhost:8000/api/brand/`);
        this.brand_lists = brands.data;
        this.listTagHirarchy();
    },
    methods: {
        async listTagHirarchy(){
           let listTags = await axios.get(`http://localhost:8000/api/tagHirarchy/`);
           this.tag_hirarchy = listTags.data;
           this.tag_hirarchy.forEach((data)=>{
             this.subCatList[data.category.tagName] = data.subcategory;
           })
       },

       addProduct(){
           const {BrandID, ProductCode, ProductName, ProductPrice, selectedCategory, selectedSubCategory, GST, Discount, Availability, Status, ProductDesc,ColorDesc} = this;
           if(!BrandID || !ProductCode || !ProductName || !ProductPrice || !selectedCategory || !selectedSubCategory){
               return;
           }

           var data = JSON.stringify({
            "productCode": ProductCode,
            "productName": ProductName,
            "productPrice": ProductPrice,
            "brand": BrandID,
            "status": Status,
            "availability": Availability,
            "gst": GST,
            "discount": Discount,
            "ProductDescription": ProductDesc,
            "colorDesc": ColorDesc
        });

        var config = {
        method: 'post',
        url: process.env.VUE_APP_SERVER_HOST+'/api/product/',
        headers: { 
            'Content-Type': 'application/json', 
            'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
        },
        data : data
        };

        axios(config)
        .then(function (response) {
        console.log(JSON.stringify(response.data));
        })
        .catch(function (error) {
        console.log(error);
        });
       },
       addProductTag(prod, cat, subCat){
           var data = JSON.stringify({
            "product": prod,
            "category": {
                "tagName": cat
            },
            "subcategory": {
                "tagName": subCat
            }
            });

            var config = {
            method: 'post',
            url: process.env.VUE_APP_SERVER_HOST+'/api/tagpivot/',
            headers: { 
                'Content-Type': 'application/json', 
                'Cookie': 'csrftoken=EUQ1DoHGzmryjVtkrFagOe7X9VbmYDz37CLuvW2AQHq6VWgfL8fj9LzjnZVo0yZI'
            },
            data : data
            };

            axios(config)
            .then(function (response) {
            console.log(JSON.stringify(response.data));
            })
            .catch(function (error) {
            console.log(error);
            });

       },
       test(){
           console.log("hii")
       }
    }

})
</script>